
import { defineComponent } from 'vue'
import { PAGE_SIZE } from '@/shared/constants/common.const'
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue'
import { APM20600111_REQ, APM20600111_RES } from '@/shared/type/APM20600111.type';
import { ConvertCode } from '@/shared/services/ConvertCodeService';
import { APM20100113_BODY, APM20100113_RES, AUTHORIZATION_CODE2_LIST } from "@/shared/type/APM20100113.type";
const convertCode = new ConvertCode();
export default defineComponent({
    name: "apm205001sc00",
    components: {
        BreadCrumb,
        Pagination,
        SelectOption
    },
    data() {
        const headerTable = [
            { columnName: "No" },
            { columnName: "View" },
            { columnName: "Authorization Biz Type Code 1" },
            { columnName: "Authorization Biz Type Code 2" },
            { columnName: "Service ID" },
            { columnName: "Service ID Description" },
            { columnName: "Service ID Name" },
            { columnName: "Status Code" },
        ];
        const search = {
            serviceID: '',
            statusCode: '',
            serviceIDName: '',
        }
        return {
            convertCode,
            headerTable,
            search,
            todoAuthorization: [],
            isReset: false,
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            filterListOut: [
                { key: '09', value: 'Deleted' }
            ],
            authorizationBizTypeCode2: [] as AUTHORIZATION_CODE2_LIST[]
        }
    },
    mounted() {
        this.getTodoAuthorizedList();
        this.inquiryAuthorizationCode2();
    },
    methods: {
        async getTodoAuthorizedList() {
            const reqBody: APM20600111_REQ = {
                pageNumber: this.currentPage,
                pageSize: this.pageSize,
                serviceID: this.search.serviceID,
                statusCode: this.search.statusCode,
                serviceIDName: this.search.serviceIDName,
            };
            const responseData: any = await this.$request.getRequest("APM20600111", reqBody, false) as APM20600111_RES;
            if (responseData.header.result) {
                this.todoAuthorization = responseData.body.list.map((item) => ({
                    serviceID: item.serviceID,
                    authorizationBizTypeCode1: item.authorizationBizTypeCode1,
                    authorizationBizTypeCode2: this.authorizationBizTypeCode2.find((type) => type.authorizationBizTypeCode2 === item.authorizationBizTypeCode2) ? `${this.authorizationBizTypeCode2.find((type) => type.authorizationBizTypeCode2 === item.authorizationBizTypeCode2)?.authorizationBizTypeCode2}: ${this.authorizationBizTypeCode2.find((type) => type.authorizationBizTypeCode2 === item.authorizationBizTypeCode2)?.managerApprovalDescription}` : item.authorizationBizTypeCode2,
                    authorizationAuthorityTypeCode: item.authorizationAuthorityTypeCode,
                    statusCode: `${item.statusCode} : ${this.$codeConverter.convertValue("BusinessCommonStatusCode", item.statusCode)} `,
                    financialTransactionYN: item.financialTransactionYN,
                    branchTypeCode: item.branchTypeCode,
                    currencyCode: item.currencyCode,
                    serviceIDName: item.serviceIDName,
                    serviceIDDescription: item.serviceIDDescription,
                }))
                this.totalItems = responseData.body.totalCount;
            }
        },

        handleRowClick(item) {
            if (!item) return
            this.$router.push({
                path: '/APM205001SC01',
                query: {
                    type: 'edit',
                    serviceID: item.serviceID,
                    authorizationBizTypeCode1: item.authorizationBizTypeCode1,
                    authorizationBizTypeCode2: item.authorizationBizTypeCode2,
                    statusCode: item.statusCode,
                }
            })
        },

        handleRegister() {
            this.$router.push({
                path: '/APM205001SC01',
                query: {
                    type: 'register'
                }
            })
        },

        handleSearch() {
            if (this.currentPage > 1) {
                this.currentPage = 1;
                this.isReset = true;
                this.getTodoAuthorizedList();
            } else {
                this.isReset = true;
                this.getTodoAuthorizedList();
            }
        },

        handlePagination(pageNumber: number) {
            this.isReset = false;
            this.currentPage = pageNumber
            this.getTodoAuthorizedList();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        },

        handleRefresh() {
            this.search = {
                serviceID: '',
                statusCode: '',
                serviceIDName: '',
            }
            this.currentPage = 1;
            this.pageSize = PAGE_SIZE;
            this.isReset = true;
            this.getTodoAuthorizedList();
        },

        statusConverter(statusCode) {
            switch (statusCode) {
                case '01: Active':
                    return 'success'
                case '02: Inactive':
                    return 'warning'
                case '03: Deleted':
                    return 'fail'
                default:
                    return "info"
            }
        },

        async inquiryAuthorizationCode2() {
            const requestBody: APM20100113_BODY = {
                authorizationBizTypeCode1: '',
            };

            const responseBody = await this.$request.getRequest("APM20100113", requestBody, false) as APM20100113_RES;
            if (responseBody.header.result) {
                this.authorizationBizTypeCode2 = responseBody.body.list.map((item) => ({
                    authorizationBizTypeCode2: item.authorizationBizTypeCode2,
                    managerApprovalDescription: item.managerApprovalDescription
                }))
            }
        },

    }

})
